
.AsteroidBox{
    margin: 10px 20px;
    border: 1px solid black;
    background-image: url("../Frame 2.png");
    background-repeat: no-repeat;
}
.testBorder{
    display: grid;
    grid-auto-rows: 1fr;
    grid-template-columns: auto 1fr auto;
    grid-template-areas: "test1 test2 test3";
}
.test2{
    top: -5px;
    position: relative;
    border-bottom: 1px dotted black;
    grid-area: test2;
}
.test1{
    grid-area: test1;
}
.test3{
    grid-area: test3;
}
.toogleSortByDistanceBtn{
}
.toogleSortByDistanceBtn.active{
    font-weight: 700;
}